.van-card {
  position: relative;
  box-sizing: border-box;
  padding: 0.34133rem 0.68267rem;
  padding: var(--card-padding, 0.34133rem 0.68267rem);
  font-size: 0.512rem;
  font-size: var(--card-font-size, 0.512rem);
  color: #323233;
  color: var(--card-text-color, #323233);
  background-color: #fafafa;
  background-color: var(--card-background-color, #fafafa);
}
.van-card__header {
  display: -webkit-flex;
  display: flex;
}
.van-card__header--center {
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}
.van-card__thumb {
  position: relative;
  -webkit-flex: none;
          flex: none;
  width: 3.75467rem;
  width: var(--card-thumb-size, 3.75467rem);
  height: 3.75467rem;
  height: var(--card-thumb-size, 3.75467rem);
  margin-right: 0.34133rem;
  margin-right: var(--padding-xs, 0.34133rem);
}
.van-card__thumb:empty {
  display: none;
}
.van-card__img {
  width: 100%;
  height: 100%;
  border-radius: 0.34133rem;
  border-radius: var(--border-radius-lg, 0.34133rem);
}
.van-card__content {
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-flex: 1;
          flex: 1;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  min-width: 0;
  /* hack for flex box ellipsis */
  min-height: 3.75467rem;
  min-height: var(--card-thumb-size, 3.75467rem);
}
.van-card__content--center {
  -webkit-justify-content: center;
          justify-content: center;
}
.van-card__title,
.van-card__desc {
  word-wrap: break-word;
}
.van-card__title {
  font-weight: bold;
  line-height: 0.68267rem;
  line-height: var(--card-title-line-height, 0.68267rem);
}
.van-card__desc {
  line-height: 0.85333rem;
  line-height: var(--card-desc-line-height, 0.85333rem);
  color: #646566;
  color: var(--card-desc-color, #646566);
}
.van-card__bottom {
  line-height: 0.85333rem;
}
.van-card__price {
  display: inline-block;
  font-weight: bold;
  color: #ee0a24;
  color: var(--card-price-color, #ee0a24);
  font-size: 0.512rem;
  font-size: var(--card-price-font-size, 0.512rem);
}
.van-card__price-integer {
  font-size: 0.68267rem;
  font-size: var(--card-price-integer-font-size, 0.68267rem);
  font-family: Avenir-Heavy, PingFang SC, Helvetica Neue, Arial, sans-serif;
  font-family: var(--card-price-font-family, Avenir-Heavy, PingFang SC, Helvetica Neue, Arial, sans-serif);
}
.van-card__price-decimal {
  font-family: Avenir-Heavy, PingFang SC, Helvetica Neue, Arial, sans-serif;
  font-family: var(--card-price-font-family, Avenir-Heavy, PingFang SC, Helvetica Neue, Arial, sans-serif);
}
.van-card__origin-price {
  display: inline-block;
  margin-left: 0.21333rem;
  text-decoration: line-through;
  font-size: 0.42667rem;
  font-size: var(--card-origin-price-font-size, 0.42667rem);
  color: #646566;
  color: var(--card-origin-price-color, #646566);
}
.van-card__num {
  float: right;
}
.van-card__tag {
  position: absolute !important;
  top: 0.08533rem;
  left: 0;
}
.van-card__footer {
  -webkit-flex: none;
          flex: none;
  width: 100%;
  text-align: right;
}
